.search-bar {
  position: relative;
  display: flex;
  margin-left: auto;
  width: 50%; }
  .search-bar img {
    position: absolute;
    background: #4CB5F5;
    border-radius: 50%;
    padding: .5rem;
    cursor: pointer;
    z-index: 1; }
  .search-bar input {
    width: 100%;
    border-radius: 1.5rem;
    padding-left: 3rem;
    height: 2.8rem;
    background: #E6E6E6;
    border: none;
    outline: none;
    -webkit-transform-origin: left;
            transform-origin: left;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards; }
  .search-bar .triangle {
    position: absolute;
    right: 1rem;
    top: 1.2rem;
    width: 0;
    height: 0;
    border-left: 0.5rem solid transparent;
    border-right: 0.5rem solid transparent;
    border-top: 0.5rem solid #666666;
    cursor: pointer; }
  .search-bar .flip {
    -webkit-animation: flip .3s ease-out;
            animation: flip .3s ease-out;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards; }
  .search-bar .fold {
    -webkit-animation: fold .3s ease-out;
            animation: fold .3s ease-out;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards; }

@-webkit-keyframes flip {
  from {
    border-radius: 50%;
    -webkit-transform: scaleX(0);
            transform: scaleX(0); }
  to {
    -webkit-transform: scaleX(1);
            transform: scaleX(1); } }

@keyframes flip {
  from {
    border-radius: 50%;
    -webkit-transform: scaleX(0);
            transform: scaleX(0); }
  to {
    -webkit-transform: scaleX(1);
            transform: scaleX(1); } }

@-webkit-keyframes fold {
  from {
    -webkit-transform: scaleX(1);
            transform: scaleX(1); }
  to {
    border-radius: 50%;
    -webkit-transform: scaleX(0);
            transform: scaleX(0); } }

@keyframes fold {
  from {
    -webkit-transform: scaleX(1);
            transform: scaleX(1); }
  to {
    border-radius: 50%;
    -webkit-transform: scaleX(0);
            transform: scaleX(0); } }

