.modal {
  display: flex;
  position: absolute;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.8);
  z-index: 10;
  justify-content: center;
  align-items: center; }
  .modal .modal-content {
    display: grid;
    grid-template-areas: "title title . . "  "input input input input"  ". cancel confirm .";
    justify-content: center;
    align-items: center;
    grid-auto-columns: 1fr;
    grid-gap: 0 .75rem;
    padding: 1rem 2rem;
    width: 450px;
    height: 220px;
    background: white;
    border-radius: 8px; }
    .modal .modal-content .title {
      grid-area: title;
