      border: none;
      outline: none;
      cursor: pointer; }
    .modal .modal-content .cancel {
      grid-area: cancel;
      color: #707070; }
    .modal .modal-content .confirm {
      grid-area: confirm;
      background: #4CB5F5;
      color: white; }

